import {API_PRODUCCION} from '../utils/constants';
import axios from 'axios';

export async function ContactarApi(form){

    try {

     const resp = await axios.post(`${API_PRODUCCION}/api/contratoes`, form).then((response) => {
          console.log(response);
        }).catch((error) => {
          console.log(error);
        });
    
        console.log(resp);
        
    } catch (error) {
        
        console.log(error);
        return null;
    }
}